<template>
  <div class="setting body-c">
    <cell-list>
      <cell-item
        path=""
        icon="icon-zhongmingming"
        content="设备总开关">
        <cell-switch
          v-model.lazy="value"
          @click.native="switchChange">
          {{valueContent}}
        </cell-switch>
      </cell-item>
      <cell-item
        path=""
        :isActive="true"
        icon="icon-zhongmingming"
        content="重命名"
        @click.native="rename">
      </cell-item>
      <cell-item
        path="/share"
        :isActive="true"
        icon="icon-tubiao212"
        content="分享">
      </cell-item>
    </cell-list>
    <cell-list>
      <cell-item
        path=""
        :isActive="true"
        icon="icon-shanchu"
        content="删除设备"
        @click.native="deleteDev">
      </cell-item>
    </cell-list>
    <cell-list>
      <cell-item
        path=""
        :isActive="true"
        icon="icon-mima1"
        content="修改密码"
        @click.native="changePassword">
      </cell-item>
      <cell-item
        path=""
        :isActive="true"
        icon="icon-mima"
        content="恢复出厂密码"
        @click.native="restorePassword">
      </cell-item>
    </cell-list>
    <cell-list>
      <cell-item
        path=""
        content="设备编号:17229900192992">
      </cell-item>
      <cell-item
        path=""
        content="设备型号:XJY-ZN-03-3P">
      </cell-item>
      <cell-item
        path=""
        content="设备产商:浙江新纪元 NEPCI">
      </cell-item>
    </cell-list>
    <cell-list class="margin-b_20">
      <cell-item
        path=""
        :isActive="true"
        icon="icon-kefu"
        content="客服电话:400-400-1111"
        @click.native="tel">
      </cell-item>
    </cell-list>
  </div>
</template>
<script>
  import {CellList, CellItem} from '../components/cell'
  import {MessageBox, Switch} from 'mint-ui'

  export default {
    name: 'Setting',
    components: {
      'cell-switch': Switch,
      'cell-list': CellList,
      'cell-item': CellItem
    },
    data () {
      return {
        value: false,
        valueContent: '关'
      }
    },
    methods: {
      //设备总开关
      switchChange: function () {
        this.value = !this.value
        if(this.value){
          this.valueContent='开'
        }else {
          this.valueContent='关'
        }
      },
      //重命名
      rename: function () {
        MessageBox.prompt('开关名称一经修改立即生效', '重命名')
          .then(({value}) => {
            //确认操作
            console.log(value)
          }).catch(() => {
          //取消操作

        })
      },
      //删除设备
      deleteDev: function () {
        MessageBox.confirm('确定删除设备?').then(action => {
          //确认操作

        }).catch(() => {
          //取消操作

        })
      },
      //修改密码
      changePassword: function () {
        MessageBox.prompt('密码一经修改立即生效', '修改密码')
          .then(({value}) => {
            //确认操作
            console.log(value)
          }).catch(() => {
          //取消操作

        })
      },
      //恢复出厂密码
      restorePassword: function () {
        MessageBox.confirm('是否恢复出厂密码?').then(action => {
          //确认操作

        }).catch(() => {
          //取消操作

        })
      },
      //客服电话
      tel: function () {
        window.location.href = 'tel:400-400-1111'
      }
    }
  }
</script>



