<template>
  <div class="home contain-footer body-c">
    <!--设备列表-->
    <dev-list>
      <dev-item
        v-for="(item,index) in devItem"
        :key="index"
        :signalActive="item.signalActive"
        :title="item.title+item.infoItem.length"
        :settingPath="item.settingPath"
        :infoItem="item.infoItem"
        :flexOne="item.infoItem.length<=1?true:false">
      </dev-item>
    </dev-list>
    <!--footer-->
    <footer-page>
      <footer-item
        @click.native="addDevice"
        icon="icon-jia"
        content="添加设备">
      </footer-item>
    </footer-page>
  </div>
</template>
<script>
  import {DevList, DevItem} from '../components/dev-list'
  import {Footer, FooterItem} from '../components/footer'
  import {MessageBox} from 'mint-ui'

  export default {
    name: 'Home',
    data () {
      return {
        devItem: [
          {
            title: '我的开关01',
            settingPath: {path: '/setting'},
            infoItem: [
              {path: {path: '/dev-switch'}, infoTitle: '通道01', infoState: '开'}
            ]
          },
          {
            title: '我的开关02',
            settingPath: {path: '/setting'},
            infoItem: [
              {path: {path: '/dev-switch'}, infoTitle: '通道01', infoState: '开'},
              {path: {path: '/dev-switch'}, infoTitle: '通道02', infoState: '开'},
              {path: {path: '/dev-switch'}, infoTitle: '通道03', infoState: '开'},
              {path: {path: '/dev-switch'}, infoTitle: '通道04', infoState: '关'}
            ]
          },
          {
            title: '我的开关03',
            settingPath: {path: '/setting'},
            signalActive: true,
            infoItem: [
              {path: {path: '/dev-switch'}, infoTitle: '通道01', infoState: '开'},
              {path: {path: '/dev-switch'}, infoTitle: '通道02', infoState: '开'},
              {path: {path: '/dev-switch'}, infoTitle: '通道03', infoState: '开'},
              {path: {path: '/dev-switch'}, infoTitle: '通道04', infoState: '关'}
            ]
          }
        ]
      }
    },
    components: {
      'dev-list': DevList,
      'dev-item': DevItem,
      'footer-page': Footer,
      'footer-item': FooterItem
    },
    created () {
//      this.$http.get('').then(function (res) {
//        console.log(res);
//      }).catch(function (res) {
//        console.log(res);
//      })
    },
    methods: {
      //删除设备
      delDevice: function () {
        MessageBox({
          title: '提示',
          message: '确定执行此操作?',
          showCancelButton: true
        })
      },
      //添加设备
      addDevice: function () {
        this.$router.push('/enter-password')
      }
    }
  }
</script>



