<template>
  <router-link
    :to="path"
    class="item-link"
    :class="{isActive:isActive}">
    <div class="cell-hb">
      <i class="iconfont nav-icon" :class="icon"></i>
    </div>
    <div class="cell-bd">{{content}}</div>
    <div class="cell-ft">
      <slot></slot>
    </div>
  </router-link>
</template>
<script>
  export default {
    name: 'CellItem',
    props: {
      path: String,
      icon: String,
      content: String,
      isActive: {
        type: Boolean,
        default: false
      }
    }
  }
</script>



